<template>
<div class="pay">

    <div class="goback-icon" @click="$router.go(-1)">
        <i class="iconfont icon-fanhui" />
    </div>

    <div class="container">
        <div>
            <div class="pay-box margin-t">
                <div class="store-info">
                    <div class="map-icon" />
                    <div class="address">
                        <h3 class="store-title">蚨来咖啡上海总店</h3>
                        <p class="store-content">上海市-静安区-万荣路-大宁中心广场B6</p>
                    </div>
                    <div class="select-btn" @click="isTake = !isTake">
                        <span class="btn" :style="{left: isTake ? '0' : '30px'}">{{isTake ? '自提' : '外送'}}</span>
                        <span class="content">{{isTake ? '' : '自提'}}</span>
                        <span class="content">{{isTake ? '外送' : ''}}</span>
                    </div>
                </div>
                <div class="time">
                    <div class="take" v-show="isTake">
                        <span>自提时间</span>
                        <span>大约{{currentTime}}制作完成</span>
                    </div>
                    <div class="address-title" v-show="!isTake" @click="showAddress">
                        <!-- <span>选择收货地址</span>
                        <van-icon name="arrow" /> -->
                        <div class="default-box">
                            <h3 class="default-info">
                                <span class="name">{{currentAddress.name}}</span>
                                <span class="tel">{{currentAddress.tel}}</span>
                            </h3>
                            <p class="default-address">
                                {{currentAddress.address}}
                            </p>
                            <div class="default-arrow">
                                <van-icon name="arrow" />
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="pay-box">
                <ul>
                    <li class="pay-item border-bottom-1px" v-for="(good,index) in goodsList" :key="index">
                        <div class="pay-img">
                            <img class="img-layout" :src="good.small_img" alt="">
                        </div>
                        <div class="pay-info">
                            <h3 class="name">{{good.name}}<span class="pay-count">X{{good.count}}</span></h3>
                            <p class="type">{{good.rule}}</p>
                        </div>
                        <div class="pay-price">
                            ￥{{good.price}}
                        </div>
                    </li>
                </ul>
                <p class="total-price">
                    合计：￥<span class="price">{{total}}</span>
                </p>
            </div>

            <div class="pay-box">
                <van-cell :border="false" title="备注特殊要求" value="添加备注" @click="showRemarks = true" />
                <van-action-sheet v-model="showRemarks" title="添加备注">
                    <van-field v-model="message" rows="5" type="textarea" maxlength="100" placeholder="请输入备注" show-word-limit />
                </van-action-sheet>
            </div>
        </div>

    </div>

    <van-action-sheet v-model="isShow" title="选择收货地址">
        <van-address-list 
            v-model="chosenAddressId"
            :list="list"
            default-tag-text="默认"
            @select="select"
            @add="increaseAddress"/>
            
    </van-action-sheet>

    <div class="pay-footer">
        <p class="total">合计：￥<span class="price">{{total}}</span></p>
        <button class="btn" @click="pay">去支付</button>
    </div>

</div>
</template>

<script>
import '../assets/scss/pay.scss'
import {
	mapGetters
} from 'vuex'
import { utils } from '../assets/js/utils'

export default {
    data() {
        return {
            sids: this.$route.query.sids,
            goodsList: [],
            isTake: true,
            showRemarks: false,
            message: '', // 备注信息
            isShow: false,
            chosenAddressId: '',
            list: [],
            currentAddress: {},
            currentTime: '',
            total: 0
        }
    },
    created() {
        this.getCurrentTime();
        this.findAddress();
        this.findShopcart();
    },
	methods: {
        // 获取当前时间
        getCurrentTime() {
            let date = new Date();
            let hours = date.getHours();
            let minutes = date.getMinutes();
            let time = ""
            if(Number(minutes) + 5 < 60) {
                minutes += 5;
                if(minutes.toString().length === 1) {        
                    minutes = "0" + minutes;
                }

                time = `${date.getHours()}:${minutes}`;
            }else {
                minutes = ''.toString(Number(minutes) + 5);
                console.log(minutes);
                minutes = '0' + minutes.slice(1);
                console.log(minutes);
                hours = Number(hours) + 1;
                time = `${hours}:${minutes}`
            }
            console.log(time);
            this.currentTime = time;
        },
        // 支付
        pay() {
             // 判断是否登录过
            if (!this.getToken) {
                this.$router.push({
                    name: 'Login'
                })
                return;
            }
            
            this.$toast({
				type: 'loading',
				message: '加载中',
				forbidClick: true,
				loadingType: 'spinner',
				duration: 0
            })

            let type =  this.isTake ? '-自提' : '-外送';
            
            let data = utils.queryString({
                appkey: this.appkey,
                tokenString: this.getToken,
                sids: JSON.stringify(this.sids),
                phone: this.currentAddress.tel,
                address: this.currentAddress.address,
                receiver: this.currentAddress.name + type
            })

			this.axios({
				method: 'POST',
				url: '/pay',
				data
			})
			.then(res => {
				this.$toast.clear();
                console.log(res);
                this.$toast(res.data.msg);
                if(res.data.code === 60000) {
                    this.$router.replace({
                        name: 'Order'
                    })
                }
			})
			.catch(err => {
				this.$toast.clear();
                console.log(err);
			})

        },
        // 查询需要购买商品
        findShopcart() {
            // 判断是否登录过
            if (!this.getToken) {
                this.$router.push({
                    name: 'Login'
                })
                return;
            }
            
            // 解决切换到新增地址页面时返回提交页面this.sids变成了字符串
            if(!Array.isArray(this.sids)) {
                this.sids = [this.sids]
            }

            let params = {
                appkey: this.appkey,
                tokenString: this.getToken,
                sids: JSON.stringify(this.sids)
            }

            this.$toast({
				type: 'loading',
				message: '加载中',
				forbidClick: true,
				loadingType: 'spinner',
				duration: 0
			})

			this.axios({
				method: 'GET',
				url: '/commitShopcart',
				params
			})
			.then(res => {
				this.$toast.clear();
                console.log(res);
                if(res.data.code === 50000) {
                    res.data.result.forEach(item => {
                        this.total += item.count * item.price;
                    })

                    this.goodsList = res.data.result;
                }
			})
			.catch(err => {
				this.$toast.clear();
                console.log(err);
			})
        },
        // 切换地址
        select(item) {
            this.currentAddress = item;
            this.isShow = false;
        },
		// 新增地址
		increaseAddress() {
			this.$router.push({
				name: 'Address'
			})
		},
		// 选择地址
		showAddress() {
			this.isShow = true;
		},
		// 查询地址
		findAddress() {

            if (!this.getToken) {
                this.$router.push({
                    name: 'Login'
                })
                return;
			}

			let params = {
				appkey: this.appkey,
				tokenString: this.getToken
			}

			this.$toast({
				type: 'loading',
				message: '加载中',
				forbidClick: true,
				loadingType: 'spinner',
				duration: 0
			})

			this.axios({
				method: 'GET',
				url: '/findAddress',
				params
			})
			.then(res => {
				this.$toast.clear();
                console.log(res);
                let arr= [];
                if(res.data.code === 20000) {
                    res.data.result.forEach(item => {
                        let province = item.province === item.city ? item.province : item.province + item.city;

                        item.address = `${province}${item.county}${item.addressDetail}`
                        // 默认地址
                        if(item.isDefault === 1) {
                            this.chosenAddressId = item.id
                            this.currentAddress = item;
                            arr.unshift(item)
                        }else {
                            arr.push(item)
                        }
                        
                    })

                    this.list = arr;
                }
			})
			.catch(err => {
				this.$toast.clear();
				console.log(err);
			})
		}
	},
	computed: {
		...mapGetters(['getToken'])
	}
}
</script>
